@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
header{background:grey;color:white;font-weight:500;padding:10px;display:flex;justify-content:space-between}header label{font-weight:700;font-size:28px}main{padding:10px}body{margin:0;font-family:"Lato", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}table{font-family:arial, sans-serif;border-collapse:collapse;width:100%}td,th{border:1px solid #dddddd;text-align:left;padding:8px}th{text-transform:capitalize}tr:nth-child(even){background-color:#dddddd}

.file:focus{outline:none;border:0}.file::-webkit-file-upload-button{color:white;display:inline-block;background:#1cb6e0;border:none;padding:7px 15px;font-weight:700;border-radius:3px;white-space:nowrap;cursor:pointer;font-size:10pt}.btn{background:transparent;border:0;cursor:pointer}.btn.primary{color:green}.btn.primary.inverted{color:white;background:green}.btn.danger{color:red}.btn.danger.inverted{color:white;background:red}.popup{position:fixed;top:0;left:0;bottom:0;right:0;padding:20px;background:rgba(0,0,0,0.7);z-index:100;display:flex;justify-content:center;align-items:center;color:white}.popup textarea{width:100%;height:250px}.popup textarea:focus{outline:0}.popup .cta{display:flex;justify-content:space-between}.popup button{margin-top:20px;background:white;padding:10px 20px}.popup button.cancel{color:grey}

